.post-inner {
  display: flex;
  max-width: 800px;
  margin: 40px 30px 0px 35px;
}

.post-inner__wrapper {
  width: 100%;
}

.post__h3 {
  font-size: 22px;
  font-weight: 600;
  margin-bottom: 10px;
}

.post__textarea {
  width: 100%;
  min-height: 80px;
  font-family: inherit;
  font-size: 16px;
  border: none;
  border-radius: 5px;
  background-color: rgb(230, 229, 229);
  padding: 10px 0px 0px 15px;
}

.post__btn {
  position: sticky;
  left: 100%;
  width: 120px;
  height: 30px;
  font-size: 16px;
  border-radius: 5px;
  margin-top: 10px;
  margin-right: -15px;
}

.post__btn:hover {
  color: aliceblue;
  background-color: gold;
}
